:root {
  --font-family: Montserrat, sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --accent-color: #5680b0;
  --dark-color: #6490c2;
  --text-dark-color: #000;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/MontserratRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/MontserratMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/MontserratSemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/MontserratBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  color: var(--light-color);
  background-color: var(--accent-color);
}
.header img:not(:last-child) {
  margin-right: 10px;
}
.header__top {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 10px 0;
  font-size: 14px;
  border-bottom: 1px solid var(--light-color);
  transition: all 0.3s ease-in-out;
}
.header__top-text {
  margin: 0;
  color: var(--light-color);
}
.header__top:hover {
  cursor: pointer;
  background-color: var(--secondary-color);
}
.header__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}
.header__content {
  display: flex;
}
.header__title {
  margin: 0;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0.035em;
  width: 400px;
  text-align: center;
}
.header__text {
  margin: 0;
  margin-top: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.035em;
}
.header__addres {
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0.035em;
  width: 300px;
}
.header__addres-link {
  color: var(--light-color);
}
.header__addres span {
  font-weight: 600;
}
.header__social {
  font-weight: 700;
  font-size: 20px;
  color: var(--light-color);
}

.nav {
  background-color: var(--accent-color);
}
.nav__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__buttons {
  display: flex;
  flex: 0 0 80px;
  margin-left: 10px;
}
.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 1 auto;
}
.nav__item {
  width: 100%;
  position: relative;
}
.nav__link {
  color: var(--light-color);
  font-size: 16px;
  padding: 10px;
  display: block;
  transition: all 0.3s ease-in-out;
  width: 100%;
}
.nav__link:hover {
  background-color: #6490c2;
}
.nav .dopmenu {
  display: flex;
  align-items: center;
}
.nav .dopmenu::after {
  content: "";
  width: 0;
  height: 0;
  margin-left: 10px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 6px solid var(--light-color);
  transition: all 0.3s ease-in-out;
}
.nav__sublist {
  background: var(--light-color);
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 100;
  width: 100%;
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0;
  transition: 0.2s ease-in-out;
}
.nav__subitem {
  padding: 7px;
}
.nav__subitem:hover {
  background-color: rgba(86, 128, 176, 0.12);
}
.nav__sublink {
  display: inline-block;
  padding: 5px;
  color: #000;
  width: 100%;
  font-size: 16px;
}

.nav__list > li:hover .nav__sublist {
  opacity: 1;
  transform: scaleY(1);
}

.nav__list > li:hover .dopmenu::after {
  transform: translateY(3px);
}

.burger {
  width: 30px;
  height: 30px;
  margin-right: 15px;
  border-radius: 50px;
  background-color: var(--light-color);
  position: relative;
}
.burger__line {
  content: "";
  width: 17px;
  height: 2px;
  border-radius: 6px;
  background-color: var(--accent-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}
.burger__line::after, .burger__line:before {
  content: "";
  width: 17px;
  height: 2px;
  border-radius: 6px;
  background-color: var(--accent-color);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.burger__line::after {
  margin-top: 5px;
}
.burger__line::before {
  margin-top: -5px;
}

.search {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: var(--light-color) url("../img/search.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero {
  background-image: url("../img/bg-hero2.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 80vh;
  position: relative;
}
.hero__title {
  margin: 0;
  width: 70%;
  color: var(--accent-color);
  padding: 50px 30px;
  font-size: 63px;
  font-weight: 700;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(30%);
}
.hero__title::after {
  content: "";
  background-color: var(--light-color);
  opacity: 0.9;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}

.banner {
  padding: 30px 0;
  background-color: #ebeff9;
}
.banner__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
.banner__item {
  height: 150px;
  color: #ebeff9;
  font-size: 30px;
  background-color: var(--accent-color);
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.message {
  background-image: url("../img/bg-message2.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 40px 0;
  margin-bottom: 40px;
  height: 40vh;
}
.message__inner {
  display: flex;
  align-items: center;
  justify-content: center;
}
.message__title {
  margin: 0;
  margin-bottom: 10px;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 600;
  color: var(--light-color);
}
.message__subtitle {
  margin: 0;
  margin-bottom: 20px;
  font-size: 20px;
  line-height: 1.2;
  font-weight: 400;
  color: var(--light-color);
}
.message__btn {
  padding: 10px;
  background-color: var(--light-color);
}